import { Box, LinearProgress } from '@material-ui/core';
import React from 'react';
import { booleanCheckCondition } from '../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useProfilePageStyles } from '../styles';
import { ProfileDataSection } from './profileDataSection/ProfileDataSection';
import { ProfilePostsSection } from './profilePostsSection/ProfilePostsSection';

export const ProfilePageContent = (props) => {

    const { profile } = props

    const {
        profile_$wrapper
    } = useProfilePageStyles()

    return booleanCheckCondition(
        profile,
        <Box className={profile_$wrapper}>
            <ProfileDataSection {...props} />
            <ProfilePostsSection {...props} />
        </Box>,
        <LinearProgress />
    )
};