import { makeStyles } from '@material-ui/core'

export const useEditModeBtnStyles = makeStyles((theme) => ({
    editModeBtn_$edit: {
        marginLeft: '10px',
        position: 'sticky',
        top: '0'
    },

    editModeBtn_$check: {
        marginLeft: '0',
        marginTop: '20px',
        position: 'relative',
        top: 'auto',
        width: '100%',
        border: '1px solid #bdbdbd',
        borderRadius: '4px'
    },

    editModeBtn: {
        color: '#333333',
        width: '100%'
    },

    editModeIcon: {
        [theme.breakpoints.down('400')]: {
            fontSize: '22px'
        }
    }
}))