import { Paper } from '@material-ui/core';
import React, { useCallback } from 'react';
import { memo } from 'react';
import { booleanCheckCondition } from '../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useProfilePageStyles } from '../../styles';
import { ProfileDataLogoBlock } from './profileDataLogoBlock/ProfileDataLogoBlock';
import { ProfileInfo } from './profileInfo/ProfileInfo';

export const ProfileDataSection = memo(({
    id,
    profile,
    status,
    isOwner,
    saveProfilePhotoT,
    setProfileSettingsT,
    updateProfileStatusT,
}) => {

    const {
        profile__dataSection_$wrapper
    } = useProfilePageStyles()

    const updateProfilePhoto = useCallback(({ target: { files } }) => {
        return booleanCheckCondition(files.length, saveProfilePhotoT(files[0]))
    }, [saveProfilePhotoT])

    return (
        <section>
            <Paper className={profile__dataSection_$wrapper}>
                <ProfileDataLogoBlock
                    profile={profile}
                    isOwner={isOwner}
                    updateProfilePhoto={updateProfilePhoto} />
                <ProfileInfo
                    profile={profile}
                    status={status}
                    id={id}
                    isOwner={isOwner}
                    setProfileSettingsT={setProfileSettingsT}
                    updateProfileStatusT={updateProfileStatusT} />
            </Paper>
        </section>
    );
});