import { Box } from '@material-ui/core';
import React, { useState } from 'react';
import { memo } from 'react';
import { booleanCheckCondition } from '../../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useProfilePageStyles } from '../../../styles';
import { ProfileInfoBlock } from './profileInfoBlock/ProfileInfoBlock';
import { ProfileInfoBlockForm } from './profileInfoBlockForm/ProfileInfoBlockForm';

export const ProfileInfo = memo(({
    profile,
    id,
    setProfileSettingsT,
    isOwner,
    status,
    updateProfileStatusT
}) => {

    const {
        dataSection__userInfo,
        userInfo_$wrapper,
    } = useProfilePageStyles()

    const [editMode, setEditMode] = useState(false)

    const asyncSetProfileSettingsAndShutdownEditMode = async (profileData) => {
        await setProfileSettingsT(profileData, id)
        return setEditMode(false)
    }

    return (
        <Box className={dataSection__userInfo}>
            <Box className={userInfo_$wrapper}>
                {booleanCheckCondition(
                    editMode,
                    <ProfileInfoBlockForm
                        profile={profile}
                        isOwner={isOwner}
                        editMode={editMode}
                        initialValues={profile}
                        onSubmit={asyncSetProfileSettingsAndShutdownEditMode}
                    />,
                    <ProfileInfoBlock
                        profile={profile}
                        isOwner={isOwner}
                        editMode={editMode}
                        goToEditMode={setEditMode}
                        status={status}
                        updateProfileStatusT={updateProfileStatusT}
                    />
                )}
            </Box>
        </Box>
    );
})