import { Box } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { compose } from 'redux';
import { reduxForm } from 'redux-form';
import { loopThroughArrayWithCallback } from '../../../../../../utils/arrayFunctions/loopThroughArrayWithCallback';
import { booleanCheckCondition } from '../../../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { EditModeBtn } from '../../../../../common/editModeBtn/EditModeBtn';
import { FormInputField } from '../../../../../common/inputs/formInputField/FormInputField';
import { InputCheckbox } from '../../../../../common/inputs/inputCheckbox/InputCheckbox';
import { InputStandard } from '../../../../../common/inputs/inputStandard/InputStandard';
import { required } from '../../../../../common/inputs/validate/ValidateInput';
import { useProfilePageStyles } from '../../../../styles';
import { BlockWrapper } from '../profileInfoBlock/blockWrapper/BlockWrapper';
import { DescriptionItem } from '../profileInfoBlock/descriptionItem/DescriptionItem';

const FormContent = ({
    profile: {
        contacts
    },
    isOwner,
    editMode,
    handleSubmit
}) => {

    const {
        userInfo_$form,
        userInfo__content
    } = useProfilePageStyles()

    const contactsLists = loopThroughArrayWithCallback(
        Object.keys(contacts),
        (contact) => {
            return (
                <DescriptionItem key={`contact_${contact}`} title={contact} isFormField={true}>
                    <FormInputField
                        name={`contacts.${contact}`}
                        component={InputStandard}
                        validations={[]}
                        castomize={{
                            type: 'text',
                            label: contact,
                            variant: 'outlined',
                            size: 'small',
                            'aria-label': `Contact ${contact}`,
                            inputMode: 'url',
                            style: {
                                width: '100%'
                            }
                        }} />
                </DescriptionItem>
            )
        }
    )

    return (
        <form id='changeProfileData' onSubmit={handleSubmit} className={userInfo_$form}>
            <Box className={userInfo__content}>
                <BlockWrapper title='About me'>
                    <DescriptionItem title='Name' isFormField={true}>
                        <FormInputField
                            name='fullName'
                            component={InputStandard}
                            validations={[required]}
                            castomize={{
                                type: 'text',
                                variant: 'outlined',
                                size: 'small',
                                'aria-label': 'Full name',
                                'aria-required': true,
                                inputMode: 'text',
                                style: {
                                    width: '100%'
                                }
                            }} />
                    </DescriptionItem>
                    <DescriptionItem title='Me' isFormField={true}>
                        <FormInputField
                            name='aboutMe'
                            component={InputStandard}
                            validations={[required]}
                            castomize={{
                                type: 'text',
                                variant: 'outlined',
                                size: 'small',
                                'aria-label': 'About me',
                                'aria-required': true,
                                inputMode: 'text',
                                style: {
                                    width: '100%'
                                }
                            }} />
                    </DescriptionItem>
                </BlockWrapper>

                <BlockWrapper title='About work'>
                    <DescriptionItem title='Find a job' isFormField={true}>
                        <FormInputField
                            name='lookingForAJob'
                            component={InputCheckbox}
                            validations={[required]}
                            castomize={{
                                type: 'checkbox',
                                size: 'small',
                                'aria-label': 'Find a job'
                            }} />
                    </DescriptionItem>
                    <DescriptionItem title='About find a job' isFormField={true}>
                        <FormInputField
                            name='lookingForAJobDescription'
                            component={InputStandard}
                            validations={[required]}
                            castomize={{
                                type: 'text',
                                variant: 'outlined',
                                size: 'small',
                                'aria-label': 'About find a job',
                                'aria-required': true,
                                inputMode: 'text',
                                style: {
                                    width: '100%'
                                }
                            }} />
                    </DescriptionItem>
                </BlockWrapper>

                <BlockWrapper title='Contacts'>
                    {contactsLists}
                </BlockWrapper>
            </Box>

            {booleanCheckCondition(
                isOwner,
                <EditModeBtn
                    formID='changeProfileData'
                    editMode={editMode} />
            )}
        </form>
    );
};

export const ProfileInfoBlockForm = compose(
    reduxForm({ form: 'edit-profile-settings' }),
    memo
)(FormContent)

