import { Box, Typography } from '@material-ui/core';
import React, { memo } from 'react';
import { booleanCheckCondition } from '../../../../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useProfilePageStyles } from '../../../../../styles';

export const BlockWrapper = memo(({
    children,
    title
}) => {
    const {
        userInfo__element,
        userInfo__element_$title,
        userInfo__element__content
    } = useProfilePageStyles()

    return booleanCheckCondition(
        children,
        <Box className={userInfo__element}>
            <Typography variant='subtitle1' className={userInfo__element_$title}>{title}</Typography>
            <ul className={userInfo__element__content}>
                {children}
            </ul>
        </Box>
    );
});