import { Box, Typography } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { useProfilePageStyles } from '../../../../../styles';

export const DescriptionItem = memo(({
    children,
    title,
    isFormField
}) => {

    const {
        element__content,
        element__content_$title,
        element__content_$value
    } = useProfilePageStyles()

    return (
        <li
            style={{ height: isFormField ? '80px' : 'auto' }}
            className={element__content}>
            <Box className={element__content_$title}>
                <Typography variant='body2'>
                    {title}
                </Typography>
            </Box>
            <Box className={element__content_$value}>
                {children}
            </Box>
        </li>
    );
});