import { makeStyles } from '@material-ui/core';

export const useProfilePageStyles = makeStyles((theme) => ({
    profile: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'stretch'
    },

    profile_$wrapper: {
        maxWidth: '100%',
        paddingTop: '20px',
        [theme.breakpoints.down('sm')]: {
            paddingTop: '0'
        }
    },

    profile__dataSection_$wrapper: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'flex-start',
        maxWidth: '100%',
        height: '100%',
        padding: '15px',
        backgroundColor: '#ffffff',
        [theme.breakpoints.down('850')]: {
            flexDirection: 'column'
        }
    },

    profile__postsSection_$wrapper: {
        maxWidth: '100%',
        padding: '15px',
        marginTop: '20px',
        backgroundColor: '#ffffff',
        color: '#333333',
        [theme.breakpoints.down('sm')]: {
            marginBottom: '70px'
        },
        [theme.breakpoints.down('xs')]: {
            marginBottom: '60px'
        }
    },

    profile__logo_$wrapper: {
        padding: '15px',
        backgroundColor: '#e7e7e7',
        borderRadius: '5px',
        marginRight: '10px',
        [theme.breakpoints.down('850')]: {
            marginBottom: '15px',
            backgroundColor: '#e7e7e7',
            width: '100%',
            height: '100%',
            marginRight: '0px'
        }
    },

    dataSection__userInfo: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'stretch',
        boxSizing: 'border-box',
        width: '100%',
        padding: '15px',
        height: '400px',
        backgroundColor: '#e7e7e7',
        borderRadius: '5px',
        overflowY: 'auto',
    },

    profile__logo: {
        width: '100%',
        height: '100%',
        display: 'flex',
        borderRadius: '5px',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'center',
        [theme.breakpoints.down('850')]: {
            padding: '15px',
            backgroundColor: '#dadada'
        }
    },

    profile__logo_$addLogo: {
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        flexDirection: 'row',
        justifyContent: 'center',
        marginTop: '15px'
    },

    profile__logo_$btn: {
        width: '100%',
        border: '1px solid #33333333',
        backgroundColor: '#dad8d8'
    },

    userInfo_$wrapper: {
        maxWidth: '100%',
        overflowWrap: 'break-word',
    },

    logo: {
        maxWidth: '100%',
        maxHeight: '100%',
        borderRadius: '5px',
        [theme.breakpoints.down('850')]: {
            maxHeight: '120px'
        }
    },

    userInfo_$form: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'flex-start'
    },

    profileInfo__content: {
        width: '100%',
        display: 'flex',
        alignItems: 'flex-start',
        flexDirection: 'row',
        justifyContent: 'space-between',
        marginBottom: '20px'
    },

    userInfo__content: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'stretch',
        width: '100%',
        borderRadius: '5px',
    },

    userInfo__element: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'center',
        marginBottom: '10px',
        maxWidth: '100%',
        backgroundColor: '#dad8d8',
        padding: '10px 20px',
        borderRadius: '5px',
        '&:last-child': {
            marginBottom: '0'
        }
    },

    userInfo__element_$title: {
        marginBottom: '20px'
    },

    userInfo__element__content: {
        width: '100%',
    },

    element__content: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'center',
        marginBottom: '10px',
        [theme.breakpoints.down('500')]: {
            alignItems: 'flex-start',
            flexDirection: 'column'
        }
    },

    element__content_$title: {
        width: '33%',
        color: 'rgba(0 0 0 / 70%)',
        [theme.breakpoints.down('xs')]: {
            width: '50%'
        },
        [theme.breakpoints.down('500')]: {
            width: '100%'
        }
    },

    element__content_$value: {
        width: '100%',
        fontWeight: '300',
        marginLeft: '20px',
        padding: '0 10px',
        height: '100%',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'stretch',
        alignItems: 'center',
        [theme.breakpoints.down('850')]: {
            marginLeft: '0'
        },
        [theme.breakpoints.down('500')]: {
            padding: '10px',
        }
    },

    profileInfo__content_$main: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'flex-start'
    },

    profile__status: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'center'
    },

    profile__posts: {},

    post: {
        marginBottom: '20px',
        '&:last-child': {
            marginBottom: '0'
        }
    },

    post_$wrapper: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'stretch',
        padding: '20px',
        paddingBottom: '30px',
        maxWidth: '100%',
        backgroundColor: '#f6f6f6',
        [theme.breakpoints.down('425')]: {
            padding: '15px',
            paddingBottom: '30px',
        },
        [theme.breakpoints.down('400')]: {
            flexDirection: 'column',
            alignItems: 'center'
        }
    },

    post__content: {
        display: 'flex',
        flexDirection: 'row',
    },

    post_avatar: {
        borderRadius: '100%',
        marginRight: '20px',
        [theme.breakpoints.down('400')]: {
            marginRight: '0',
            marginBottom: '10px'
        }
    },

    avatar: {
        width: '50px',
        height: '50px',
        [theme.breakpoints.down('425')]: {
            width: '45px',
            height: '45px'
        },
        [theme.breakpoints.down('350')]: {
            width: '42px',
            height: '42px',
        }
    },

    post__info: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'flex-start',
        [theme.breakpoints.down('400')]: {
            alignItems: 'center',
        }
    },

    post__info_$title: {
        marginBottom: '15px',
        [theme.breakpoints.down('400')]: {
            textAlign: 'center',
            marginBottom: '10px'
        }
    },

    titleText: {
        fontWeight: '600',
        lineHeight: '1.4'
    },

    bodyText: {
        maxWidth: '600px',
        [theme.breakpoints.down('400')]: {
            textAlign: 'center',
        }
    },

    addPostsForm: {
        marginBottom: '15px'
    },

    addPosts: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'flex-start',
        marginBottom: '20px',
        padding: '20px',
        width: '100%',
        borderRadius: '3px',
        backgroundColor: '#ffffff',
    },

    addPosts__fields: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'space-between',
        height: '100%',
        width: '100%',
        marginBottom: '40px'
    },

    addPosts__fields_$title: {
        width: '100%',
        marginBottom: '20px'
    },

    addPosts__fields_$body: {
        width: '100%'
    },

    addPosts__fields_$btn: {
        width: '100%'
    },

    editBtnActive: {
        width: 'auto',
        marginTop: '0',
        marginLeft: '0'
    }
}))