import { Box } from '@material-ui/core';
import React from 'react';
import { useProfilePageStyles } from '../../../styles';
import defaultProfileLogo from '../../../../../assets/images/Default-profile-logo.png'
import { UpdateLogoPhoto } from '../updateLogoPhoto/UpdateLogoPhoto';
import { memo } from 'react';

export const ProfileDataLogoBlock = memo(({
    profile: {
        id,
        photos: {
            large
        }
    },
    updateProfilePhoto,
    isOwner,
}) => {

    const {
        profile__logo_$wrapper,
        profile__logo,
        logo,
    } = useProfilePageStyles()

    return (
        <Box className={profile__logo_$wrapper}>
            <div className={profile__logo}>
                <img
                    alt='mainUserLogo'
                    className={logo}
                    src={large || defaultProfileLogo} />
                <UpdateLogoPhoto isOwner={isOwner} updateProfilePhoto={updateProfilePhoto} />
            </div>
        </Box>
    );
})