import React, { memo, useCallback, useEffect } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { compose } from 'redux';
import {
    getProfileStatusT,
    getProfileUserPostsT,
    saveProfilePhotoT,
    setProfileDataT,
    setProfileSettingsT,
    updateProfileStatusT,
    addProfilePostT,
    removeProfilePostT
} from '../../redux/reducers/profile-reducer';
import { getIsAuth, getUserId } from '../../redux/selectors/auth-selectors';
import {
    getProfileUserInfo,
    getProfileUserPosts,
    getUserStatus
} from '../../redux/selectors/profile-selectors';
import { booleanCheckCondition } from '../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { withAuthRedirect } from '../common/hoc/withAuthRedirect/withAuthRedirect';
import { Profile } from './Profile';


const ProfileContainer = (props) => {

    const {
        id,
        isAuth,
        setProfileDataT,
        getProfileUserPostsT,
        getProfileStatusT,
        match: {
            params: {
                userId
            }
        }
    } = props

    const isOwner = booleanCheckCondition(+userId === id)

    const refreshProfilePage = useCallback(() => {
        let profileId = userId;
        if (!profileId && isAuth) {
            profileId = id
        }
        setProfileDataT(profileId);
        getProfileStatusT(profileId);
        getProfileUserPostsT(profileId)
    }, [id, userId, isAuth, setProfileDataT, getProfileStatusT, getProfileUserPostsT])

    useEffect(() => {
        refreshProfilePage()
    }, [refreshProfilePage])

    return <Profile {...props} isOwner={isOwner} />
};


export default compose(
    connect((state) => ({
        profile: getProfileUserInfo(state),
        id: getUserId(state),
        isAuth: getIsAuth(state),
        status: getUserStatus(state),
        posts: getProfileUserPosts(state)
    }), {
        setProfileDataT,
        setProfileSettingsT,
        saveProfilePhotoT,
        getProfileStatusT,
        updateProfileStatusT,
        getProfileUserPostsT,
        addProfilePostT,
        removeProfilePostT
    }),
    memo,
    withRouter,
    withAuthRedirect
)(ProfileContainer, 'login', 'invert')