import React, { useState } from 'react';
import { booleanCheckCondition } from '../../../../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { ProfileStatusForm } from './profileUserStatus/ProfileStatusForm'
import { UserStatus } from '../../../../../../common/userStatus/UserStatus';
import { memo } from 'react';

export const ProfileUserStatus = memo(({
    updateProfileStatusT,
    isOwner,
    userId,
    status
}) => {

    const [editMode, setEditMode] = useState(false)

    const setProfileSettingsAndShutdownEditMode = ({ status }) => {
        updateProfileStatusT(status, userId)
        setEditMode(false)
    }

    return booleanCheckCondition(
        isOwner,
        booleanCheckCondition(
            editMode,
            <ProfileStatusForm
                statusEditMode={editMode}
                initialValues={{ status }}
                onSubmit={setProfileSettingsAndShutdownEditMode}
            />,
            <UserStatus
                status={status}
                functionChangeStatus={setEditMode} />
        ),
        <UserStatus status={status} />
    )
});