import { Box } from '@material-ui/core';
import React, { memo, Profiler } from 'react';
import DocumentTitle from 'react-document-title';
import { LocationPage } from '../common/locationPage/LocationPage';
import { profilerOnRender } from '../common/profiler/profilerOnRender';
import { ProfilePageContent } from './profilePageContent/ProfilePageContent';
import { useProfilePageStyles } from './styles';

export const Profile = memo((props) => {

    const { profile } = useProfilePageStyles()

    return (
        <Box className={profile}>
            <DocumentTitle title='Profile' />
            <LocationPage location='Profile' />
            <Profiler id='profile-profiler' onRender={profilerOnRender}>
                <ProfilePageContent {...props} />
            </Profiler>
        </Box>
    )
});