import { Box, Link, Typography } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { loopThroughArrayWithCallback } from '../../../../../../utils/arrayFunctions/loopThroughArrayWithCallback';
import { booleanCheckCondition } from '../../../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { EditModeBtn } from '../../../../../common/editModeBtn/EditModeBtn';
import { useProfilePageStyles } from '../../../../styles';
import { MainProfileInfo } from '../mainProfileInfo/MainProfileInfo';
import { BlockWrapper } from './blockWrapper/BlockWrapper';
import { DescriptionItem } from './descriptionItem/DescriptionItem';

export const ProfileInfoBlock = memo(({
    profile: {
        userId,
        fullName,
        aboutMe,
        lookingForAJob,
        lookingForAJobDescription,
        contacts
    },
    isOwner,
    editMode,
    goToEditMode,
    updateProfileStatusT,
    status
}) => {

    const {
        profileInfo__content
    } = useProfilePageStyles()

    const contactsLists = loopThroughArrayWithCallback(
        Object.keys(contacts),
        (contact) => {
            return booleanCheckCondition(
                contacts[contact],
                <DescriptionItem key={`contact_${contact}`} title={contact} >
                    <Link
                        href={contacts[contact]}
                        target='_blank'
                        variant='body2'>
                        {contacts[contact]}
                    </Link>
                </DescriptionItem>
            )
        }
    )

    return (
        <Box>
            <Box className={profileInfo__content}>
                <MainProfileInfo
                    userId={userId}
                    fullName={fullName}
                    isOwner={isOwner}
                    status={status}
                    updateProfileStatusT={updateProfileStatusT} />

                {booleanCheckCondition(
                    isOwner,
                    <EditModeBtn
                        editMode={editMode}
                        goToEditMode={goToEditMode} />
                )}
            </Box>

            <BlockWrapper title='About me'>
                <DescriptionItem title='Me'>
                    <Typography variant='body2'>
                        {aboutMe}
                    </Typography>
                </DescriptionItem>
            </BlockWrapper>

            <BlockWrapper title='About work'>
                <DescriptionItem title='Find a job'>
                    <Typography variant='body2'>
                        {booleanCheckCondition(lookingForAJob, 'Yeas', 'No')}
                    </Typography>
                </DescriptionItem>

                <DescriptionItem title='About find a job'>
                    <Typography variant='body2'>
                        {lookingForAJobDescription}
                    </Typography>
                </DescriptionItem>
            </BlockWrapper>

            <BlockWrapper title='Contacts'>
                {contactsLists}
            </BlockWrapper>

        </Box>
    );
});