import { Accordion, AccordionDetails, AccordionSummary, Box, Button, Typography } from '@material-ui/core';
import { Check } from '@material-ui/icons';
import React from 'react';
import { compose } from 'redux';
import { reduxForm } from 'redux-form';
import { FormInputField } from '../../../../../common/inputs/formInputField/FormInputField';
import { InputStandard } from '../../../../../common/inputs/inputStandard/InputStandard';
import { required } from '../../../../../common/inputs/validate/ValidateInput';
import { useProfilePageStyles } from '../../../../styles';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { memo } from 'react';

const Wrapper = ({ handleSubmit }) => {

    const {
        addPostsForm,
        addPosts,
        addPosts__fields,
        addPosts__fields_$title,
        addPosts__fields_$body,
        addPosts__fields_$btn
    } = useProfilePageStyles()

    return (
        <form className={addPostsForm} onSubmit={handleSubmit}>
            <Accordion style={{ backgroundColor: '#ebebeb' }}>
                <AccordionSummary
                    aria-controls='add-post'
                    aria-label='add-post'
                    expandIcon={<ExpandMoreIcon />}>
                    <Typography>
                        What's new?
                    </Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Box className={addPosts}>
                        <Box className={addPosts__fields}>
                            <Box className={addPosts__fields_$title}>
                                <FormInputField
                                    name='title'
                                    component={InputStandard}
                                    validations={[required]}
                                    castomize={{
                                        type: 'text',
                                        placeholder: 'Title...',
                                        style: {
                                            width: '100%'
                                        }
                                    }} />
                            </Box>
                            <Box className={addPosts__fields_$body}>
                                <FormInputField
                                    name='body'
                                    component={InputStandard}
                                    validations={[required]}
                                    castomize={{
                                        type: 'text',
                                        placeholder: 'Your post...',
                                        style: {
                                            width: '100%',
                                        }
                                    }} />
                            </Box>
                        </Box>
                        <Box className={addPosts__fields_$btn}>
                            <Button type='submit' style={{ width: '100%' }}>
                                <Check />
                            </Button>
                        </Box>
                    </Box>
                </AccordionDetails>
            </Accordion>
        </form>
    );
};

export const AddPostForm = compose(
    reduxForm({ form: 'add-post' }),
    memo
)(Wrapper)