import { Avatar, Box, IconButton, Paper, Typography } from '@material-ui/core';
import { Delete } from '@material-ui/icons';
import React from 'react';
import { memo } from 'react';
import { NavLink } from 'react-router-dom';
import { useProfilePageStyles } from '../../../../styles';

export const Post = memo(({
    id,
    userId,
    fullName,
    body,
    title,
    photos: {
        small
    },
    removeProfilePostT
}) => {

    const {
        post,
        post_$wrapper,
        post__content,
        post_avatar,
        avatar,
        post__info,
        post__info_$title,
        titleText,
        bodyText
    } = useProfilePageStyles()

    return (
        <li className={post}>
            <Paper className={post_$wrapper}>
                <Box className={post__content}>
                    <Box className={post_avatar}>
                        <NavLink aria-label={`User link - ${fullName}`} to={`${userId}`}>
                            <Avatar className={avatar} alt='userLogo' src={small} />
                        </NavLink>
                    </Box>
                    <Box className={post__info}>
                        <Box className={post__info_$title}>
                            <Typography className={titleText} variant='subtitle1'>
                                {title}
                            </Typography>
                        </Box>
                        <Box className={bodyText}>
                            <Typography variant='body2'>
                                {body}
                            </Typography>
                        </Box>
                    </Box>
                </Box>
                <Box>
                    <IconButton aria-label="delete" onClick={() => removeProfilePostT(id)}>
                        <Delete fontSize="small" />
                    </IconButton>
                </Box>
            </Paper>
        </li>
    );
});