import React, { memo } from 'react';
import { loopThroughArrayWithCallback } from '../../../../../utils/arrayFunctions/loopThroughArrayWithCallback';
import { useProfilePageStyles } from '../../../styles';
import { Post } from './post/Post';

export const Posts = memo(({
    posts,
    profile: {
        userId,
        photos
    },
    removeProfilePostT
}) => {

    const { profile__posts } = useProfilePageStyles()

    const postsList = loopThroughArrayWithCallback(
        posts,
        (post) => (
            <Post
                key={`post_${post.id}`}
                id={post.id}
                {...post}
                userId={userId}
                photos={photos}
                removeProfilePostT={removeProfilePostT}
            />
        )
    )

    return (
        <ul className={profile__posts}>
            {[...postsList]}
        </ul>
    );
});
