import { Button } from '@material-ui/core';
import { AddAPhoto } from '@material-ui/icons';
import React, { memo } from 'react';
import { booleanCheckCondition } from '../../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useProfilePageStyles } from '../../../styles';

export const UpdateLogoPhoto = memo(({ isOwner, updateProfilePhoto }) => {

    const {
        profile__logo_$addLogo,
        profile__logo_$btn
    } = useProfilePageStyles()

    return booleanCheckCondition(
        isOwner,
        <label htmlFor='updatePhoto' className={profile__logo_$addLogo} >
            <input
                multiple
                id='updatePhoto'
                type='file'
                accept="image/*"
                onChange={updateProfilePhoto}
                aria-label='Change profile photo'
                style={{ display: 'none' }} />
            <Button className={profile__logo_$btn} component='span'>
                <AddAPhoto />
            </Button>
        </label>
    )
});