import { Paper } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { booleanCheckCondition } from '../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useProfilePageStyles } from '../../styles';
import { AddPostForm } from './posts/addPostForm/AddPostForm';
import { Posts } from './posts/Posts';

export const ProfilePostsSection = memo(({ posts, profile, addProfilePostT, removeProfilePostT, isOwner }) => {

    const reversePosts = [...posts].reverse()

    const {
        profile__postsSection_$wrapper
    } = useProfilePageStyles()

    const addProfilePost = (data) => {
        addProfilePostT(data)
    }

    return (
        <section>
            <Paper className={profile__postsSection_$wrapper}>
                {booleanCheckCondition(
                    isOwner,
                    <AddPostForm onSubmit={addProfilePost} />
                )}
                <Posts posts={reversePosts} removeProfilePostT={removeProfilePostT} profile={profile} />
            </Paper>
        </section>
    );
});