import { Box } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { useProfilePageStyles } from '../../../../styles';
import { ProfileNameBlock } from './profileNameBlock/ProfileNameBlock';
import { ProfileUserStatus } from './profileUserStatus/ProfileUserStatus';

export const MainProfileInfo = memo(({
    userId,
    fullName,
    status,
    isOwner,
    updateProfileStatusT
}) => {

    const {
        profileInfo__content_$main
    } = useProfilePageStyles()

    return (
        <Box className={profileInfo__content_$main}>
            <Box>
                <ProfileNameBlock userName={fullName} />
                <ProfileUserStatus
                    isOwner={isOwner}
                    status={status}
                    userId={userId}
                    updateProfileStatusT={updateProfileStatusT}
                />
            </Box>
        </Box>
    );
});