import { Box, Button } from '@material-ui/core';
import { Check, Edit } from '@material-ui/icons';
import React from 'react';
import { booleanCheckCondition } from '../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useEditModeBtnStyles } from './styles';
import cn from 'classnames'

export const EditModeBtn = ({
    editMode,
    goToEditMode,
    formID,
    classNameActive = ''
}) => {

    const {
        editModeBtn,
        editModeBtn_$edit,
        editModeBtn_$check,
        editModeIcon
    } = useEditModeBtnStyles()

    return (
        <Box
            aria-controls={formID}
            className={cn(editModeBtn_$edit, {
                [classNameActive || editModeBtn_$check]: editMode && true
            })}>
            <Button
                type='submit'
                onClick={() => goToEditMode && goToEditMode(!editMode)}
                aria-label='Change profile data'
                className={editModeBtn}>
                {booleanCheckCondition(
                    editMode,
                    <Check className={editModeIcon} />,
                    <Edit className={editModeIcon} />
                )}
            </Button>
        </Box>
    )
};