import { Typography } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { booleanCheckCondition } from '../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useUserStatusStyles } from './style'

export const UserStatus = memo(({ status, functionChangeStatus }) => {

    const { userStatus } = useUserStatusStyles()

    const changeStatusEditMode = booleanCheckCondition(
        functionChangeStatus,
        () => functionChangeStatus(true),
        (event) => event.preventDefault()
    )

    return (
        <Typography
            className={userStatus}
            tabIndex='0'
            variant='body2'
            onClick={changeStatusEditMode} >
            {status || <span style={{ color: '#6a6a6a' }}>Information not specified</span>}
        </Typography>
    )
});