import { Box } from '@material-ui/core';
import React, { memo } from 'react';
import { compose } from 'redux';
import { reduxForm } from 'redux-form';
import { EditModeBtn } from '../../../../../../../common/editModeBtn/EditModeBtn';
import { FormInputField } from '../../../../../../../common/inputs/formInputField/FormInputField';
import { InputStandard } from '../../../../../../../common/inputs/inputStandard/InputStandard';
import { useProfilePageStyles } from '../../../../../../styles';

export const Wrapper = ({
    handleSubmit,
    statusEditMode
}) => {

    const {
        profile__status,
        editBtnActive
    } = useProfilePageStyles()

    return (
        <form onSubmit={handleSubmit}>
            <Box className={profile__status}>
                <FormInputField
                    name='status'
                    component={InputStandard}
                    validations={[]}
                    castomize={{
                        type: 'text',
                        label: 'Status',
                        autoFocus: true,
                        placeholder: 'Set a status',
                        inputMode: 'text'
                    }} />
                <EditModeBtn
                    classNameActive={editBtnActive}
                    editMode={statusEditMode} />
            </Box>
        </form>
    );
};

export const ProfileStatusForm = compose(
    reduxForm({ form: 'profile-status' }),
    memo
)(Wrapper)